








import { Component, Vue } from 'vue-property-decorator'

const ButtonProps = Vue.extend({
  props: {
    isLoading: {
      type: Boolean,
      default: () => false,
    },
  },
})

@Component({
  name: 'AppButton',
})
export default class AppButton extends ButtonProps {
  get loader(): boolean {
    return this.isLoading
  }
}
